"use client"

import React, { useState, useRef, useEffect } from "react";
import Link from "next/link";
import { useRouter } from "next/navigation";
import {
  BookOpen,
  FileText,
  FileStack,
  BarChart2,
  MessageCircleQuestion,
  Users,
  Video,
  Calendar,
  BookMarked,
  Lightbulb,
  FileSearch,
  FileCheck2,
  FileBarChart2,
  FileCog,
  FileSignature,
  FileSpreadsheet,
  FileText as FileTextIcon,
} from "lucide-react";

const menuConfig = [
  {
    label: "Study Materials",
    icon: BookOpen,
    dropdown: [
      { label: "JEE Notes", href: "/study-materials/jee-notes", icon: FileText },
      { label: "EAPCET Notes", href: "/study-materials/eapcet-notes", icon: FileStack },
      { label: "Formula Sheets", href: "/study-materials/formula-sheets", icon: FileSpreadsheet },
      { label: "Previous Year Papers", href: "/study-materials/previous-papers", icon: FileSearch },
    ],
  },
  {
    label: "Mock Tests",
    icon: FileCheck2,
    dropdown: [
      { label: "JEE Main Mock Tests", href: "/mock-tests/jee-main", icon: FileBarChart2 },
      { label: "JEE Advanced Mock Tests", href: "/mock-tests/jee-advanced", icon: FileCog },
      { label: "EAPCET Mock Tests", href: "/mock-tests/eapcet", icon: FileSignature },
      { label: "Chapter-wise Tests", href: "/mock-tests/chapter-wise", icon: FileTextIcon },
    ],
  },
  {
    label: "Analytics",
    icon: BarChart2,
    href: "/dashboard",
  },
  {
    label: "Doubt Forum",
    icon: MessageCircleQuestion,
    href: "/doubt-forum",
  },
  {
    label: "Resources",
    icon: BookMarked,
    dropdown: [
      { label: "Video Lectures", href: "/resources/video-lectures", icon: Video },
      { label: "Reference Books", href: "/resources/reference-books", icon: BookOpen },
      { label: "Exam Calendar", href: "/resources/exam-calendar", icon: Calendar },
      { label: "Tips & Tricks", href: "/resources/tips-tricks", icon: Lightbulb },
    ],
  },
];

const navConfig = [
  { label: "Features", href: "#features" },
  { label: "Test Series", href: "#test-series" },
  { label: "Pricing", href: "#pricing" },
];

function useClickOutside(ref: React.RefObject<HTMLElement>, handler: () => void) {
  useEffect(() => {
    const listener = (event: MouseEvent) => {
      if (!ref.current || ref.current.contains(event.target as Node)) {
        return;
      }
      handler();
    };
    document.addEventListener("mousedown", listener);
    return () => document.removeEventListener("mousedown", listener);
  }, [ref, handler]);
}

export default function Navbar() {
  const [openDropdown, setOpenDropdown] = useState<string | null>(null);
  const dropdownRefs = useRef<(HTMLDivElement | null)[]>([]);
  const router = useRouter();

  // Close dropdowns on click outside
  useEffect(() => {
    function handleClick(event: MouseEvent) {
      dropdownRefs.current.forEach((ref, idx) => {
        if (ref && !ref.contains(event.target as Node)) {
          setOpenDropdown((prev) => (prev === menuConfig[idx].label ? null : prev));
        }
      });
    }
    document.addEventListener("mousedown", handleClick);
    return () => document.removeEventListener("mousedown", handleClick);
  }, []);

  // Keyboard navigation
  function handleKeyDown(e: React.KeyboardEvent, idx: number) {
    if (e.key === "Escape") setOpenDropdown(null);
    if (e.key === "ArrowDown") setOpenDropdown(menuConfig[idx].label);
    if (e.key === "Tab") setOpenDropdown(null);
  }

  return (
    <header className="sticky top-0 z-40 border-b border-gray-200 w-full py-4 flex items-center transition-all">
      <div className="flex items-center w-full max-w-7xl mx-auto px-4 md:px-8">
        <Link href="/" className="flex-shrink-0">
          <img src="/recallpro.png" alt="RecallPro Logo" width={48} height={48} className="mr-4 rounded" />
        </Link>
        <nav className="hidden md:flex items-center gap-4 lg:gap-8 font-medium text-base flex-1">
          {/* New Buttons (left of Features) */}
          {menuConfig.map((item, idx) => (
            <div
              key={item.label}
              className="relative"
              ref={(el) => { dropdownRefs.current[idx] = el; }}
              tabIndex={0}
              aria-haspopup={!!item.dropdown}
              aria-expanded={openDropdown === item.label}
              onKeyDown={(e) => handleKeyDown(e, idx)}
            >
              {item.dropdown ? (
                <button
                  className={`flex items-center gap-2 px-3 py-2 rounded-md transition-colors hover:bg-blue-50 hover:text-blue-700 focus:outline-none focus:ring-2 focus:ring-blue-400 ${openDropdown === item.label ? "bg-blue-100 text-blue-700" : "text-gray-700"}`}
                  aria-label={item.label}
                  onClick={() => setOpenDropdown(openDropdown === item.label ? null : item.label)}
                >
                  <item.icon className="w-5 h-5" />
                  {item.label}
                  <svg className="w-4 h-4 ml-1" fill="none" stroke="currentColor" strokeWidth={2} viewBox="0 0 24 24"><path strokeLinecap="round" strokeLinejoin="round" d="M19 9l-7 7-7-7" /></svg>
                </button>
              ) : (
                <Link
                  href={item.href!}
                  className="flex items-center gap-2 px-3 py-2 rounded-md transition-colors hover:bg-blue-50 hover:text-blue-700 focus:outline-none focus:ring-2 focus:ring-blue-400 text-gray-700"
                  aria-label={item.label}
                >
                  <item.icon className="w-5 h-5" />
                  {item.label}
                </Link>
              )}
              {/* Dropdown */}
              {item.dropdown && openDropdown === item.label && (
                <div
                  className="absolute left-0 mt-2 w-56 bg-white border border-gray-200 rounded-md shadow-lg z-50 animate-fade-in"
                  role="menu"
                  aria-label={item.label + " menu"}
                >
                  {item.dropdown.map((sub, subIdx) => (
                    <Link
                      key={sub.label}
                      href={sub.href}
                      className="flex items-center gap-2 px-4 py-2 text-gray-700 hover:bg-blue-100 hover:text-blue-800 transition-colors rounded-md focus:bg-blue-100 focus:text-blue-800"
                      role="menuitem"
                    >
                      <sub.icon className="w-4 h-4" />
                      {sub.label}
                    </Link>
                  ))}
                </div>
              )}
            </div>
          ))}
          {/* Existing nav items */}
          {navConfig.map((item) => (
            <a
              key={item.label}
              href={item.href}
              className="text-gray-600 hover:text-blue-600 px-3 py-2 rounded-md transition-colors"
            >
              {item.label}
            </a>
          ))}
          <Link href="/recall" className="bg-blue-600 text-white px-4 py-2 rounded-md hover:bg-blue-700 transition">Recall</Link>
          <Link href="/auth/login" className="bg-blue-600 text-white px-4 py-2 rounded-md hover:bg-blue-700 transition">Get Started</Link>
        </nav>
        {/* Mobile menu button (not implemented here) */}
        <button className="md:hidden cursor-pointer">
          <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16M4 18h16" />
          </svg>
        </button>
      </div>
    </header>
  );
} 